.services_slider1 {
    margin-top: 30px;
    width: calc(100% - 40px);
    overflow: hidden;
};

.slider_image1 {
    position: relative;
    width: 100%;
    max-width: 350px !important;
    height: 216px;
    border-radius: 24px;
    background: url("../../images/services_slider_1.png") lightgray 0px -75.67px / 100% 242.751% no-repeat;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.20);
};

