.services_slider2 {
    width: 100%;
    position: relative;
    margin: 84px 0px 0px 0px !important;
};

.slider_card {
    width: 100%;
    height: 232px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    overflow: hidden;
    margin: 0px !important;
};

.slider_text_container {
    display: flex;
    flex-direction: column;
    margin: 0px !important;
};

.slider_title_container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 0px !important;
};

.slider_title1 {
    margin: 0px;
    color: #005382;
    font-family: "Exo2-Light";
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
};

.slider_title2 {
    margin: 0px;
    color: #005382;
    font-family: "Exo2-Bold";
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
};

.slider_description {
    position: relative;
    margin-top: 20px;
    color: #181818;
    font-family: "Exo2-Regular";
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

    &::before {
        content: "";
        top: -10px;
        left: 0px;
        position: absolute;
        width: 100%;
        max-width: 350px;
        height: 1px;
        background: rgba(24, 24, 24, 0.20);
    }
};

.slider_link {
    margin-top: 20px;
    color: #00A3FF;
    font-family: "Exo2-Regular";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration: none;
};
